.recipeBox {
    /* box-sizing: border-box; */
    background-color: skyblue;

    display: inline-block;
    margin: 1vw;
    min-width: 100px;
    min-height: 100px;
    width: 10vw;
    height: 10vw;

    margin-top: auto;
    border-radius: 2rem;
}

.recipeName {
    display: block;



    text-align: center;
    background-color: slateblue;


}

.recipeDescription {
    display: block;
    overflow: auto;
    width: 100%;
    height: 100%;
}